	module.exports = {"data":{"site":{"siteMetadata":{"title":"Dan Beaven's blog","author":"Dan Beaven"}},"markdownRemark":{"id":"/Users/dan/Code/blog/src/pages/posts/07-semiotics.md absPath of file >>> MarkdownRemark","html":"<h1>What is Semiotics?</h1>\n<p>Semiotics is defined in the oxford English dictionary as “The study of signs and symbols and their use or interpretation” (semiotics, 2017). This is the first time I have come across semiotics so I use this essay to explore where it came from and what other subjects it is related to.</p>\n<p>One of the earliest times a word relating to semiotics appeared was in Swiss linguist Ferdinand de Saussure’s ‘A Course in general linguistics’, “A science that studies the life of signs within society is conceivable; it would be a part of social psychology and consequently of general psychology; I shall call it semiology” (de Saussure, 1974, p. 16). He goes on to define a sign by splitting it into two components, the signifier and signified. The signifier is the literal sign: sound, image, text and the signified is the concept or idea that the sign is referring to. Depending on your definition It may be misleading to call semiotics a science Daniel Chandler, in his book ‘Semiotics: the basics’ explains that it has “no widely agreed theoretical assumptions, models or empirical methodologies” (Chandler, 2006, p. 4).</p>\n<p>Saussure does give a sort of framework to work from when he defines the sign as being made from the signified and the signifier. Using this definition of a sign it is possible to break down and compare different signs and debate their connotations, denotations with some level of logical progression.</p>\n<p>I found it helpful to see linguistics as a subset of semiotics, it being the study of perhaps our most valued sign system language. It is interesting to step back and look at language like this, comparing it to our other sign systems such as mathematical notation, music, or body language. Looking from this perspective it is easier understand what Chandler means when he says “To decline the study of signs is to leave to others the control of the world of meaning which we inhabit.” (2006, p. 11).</p>\n<h4>References</h4>\n<p>Chandler, D. (2006) Semiotics: The basics, Vol. 2. 2nd edn. LONDON: Taylor &#x26; Francis.</p>\n<p>de Saussure, F. (1974) Course in general linguistics. Edited by Charles Bally, Albert Sechehaye, and Albert Reidlinger. Translated by Wade Baskin. 2nd edn. New York: McGraw-Hill Book Company.</p>\n<p>Peirce, C.S.S., Hartshorne, C. and Weiss, P. (1932) Collected papers of Charles Sanders Peirce, volumes I and II, principles of philosophy and elements of logic. 4th edn. Cambridge, MA: Belknap Press of Harvard University Press, 1935-1961.</p>\n<p>semiotics. (2017). In: Oxford, 1st ed. [online] Available at: <a href=\"https://en.oxforddictionaries.com/definition/semiotics\">https://en.oxforddictionaries.com/definition/semiotics</a> [Accessed 15 May 2017].</p>","frontmatter":{"title":"What is Semiotics?","date":"January 17, 2017"}}},"pathContext":{"slug":"/posts/07-semiotics/","previous":{"fields":{"slug":"/posts/06-measuring-the-impact-of-culture-on-the-economy/"},"frontmatter":{"title":"Measuring the impact of culture on the economy","description":"Each decision to fund one cause is also the decision not to fund another.","thumbnail":null,"gallery":null,"essay":null}},"next":{"fields":{"slug":"/posts/08-walk-through-british-art/"},"frontmatter":{"title":"Walk Through British Art","description":"Having an exhibition laid out in chronological order helped me to understand art and art styles in respect to history","thumbnail":"http://www.tate.org.uk/art/images/work/T/T02/T02270_10.jpg","gallery":null,"essay":null}}}}

